// Live data loading


const combineStartEnd = require("./process.js").combineStartEnd;
const combineQuestionResponses = require("./process.js").combineQuestionResponses;
const processConfig = require("./process.js").processConfig;
const processResponses = require("./process.js").processResponses;

const urls = [
  "https://forms.office.com/formapi/api/9316d1d2-47f8-4c56-924f-2ccb9bde9ac9/users/2e4b3d65-1aad-4659-bae5-59953096a62b/analysisForms('0tEWk_hHVkySTyzLm96ayWU9Sy6tGllGuuVZlTCWpitUNzFQUDBLUUlUQVJBVUJMWk1WTEZMTVU0TC4u')/GetAggregateSurveyData",
  "https://forms.office.com/formapi/api/9316d1d2-47f8-4c56-924f-2ccb9bde9ac9/users/2e4b3d65-1aad-4659-bae5-59953096a62b/analysisForms('0tEWk_hHVkySTyzLm96ayWU9Sy6tGllGuuVZlTCWpitUNzFQUDBLUUlUQVJBVUJMWk1WTEZMTVU0TC4u')?$expand=questions($expand=choices)",
  "https://forms.office.com/formapi/api/9316d1d2-47f8-4c56-924f-2ccb9bde9ac9/users/2e4b3d65-1aad-4659-bae5-59953096a62b/analysisForms('0tEWk_hHVkySTyzLm96ayWU9Sy6tGllGuuVZlTCWpitUQlEyNVBWNk1LNFI4TFQ2SVY4VTAxWEJEUC4u')/GetAggregateSurveyData",
  "https://forms.office.com/formapi/api/9316d1d2-47f8-4c56-924f-2ccb9bde9ac9/users/2e4b3d65-1aad-4659-bae5-59953096a62b/analysisForms('0tEWk_hHVkySTyzLm96ayWU9Sy6tGllGuuVZlTCWpitUQlEyNVBWNk1LNFI4TFQ2SVY4VTAxWEJEUC4u')?$expand=questions($expand=choices)"
];

const confStart = {headers: {"Analyzertoken": "rd7OWHImfz5Mw9s8M0kq7swMdlKemJWr"}};
const confEnd = {headers: {"Analyzertoken": "ljUHaqlNqvu6DeeDZ7Ikiz4A4t73sRHF"}};

const load = function(){
  return new Promise((resolve) => {

    const fetches = [
      fetch(urls[0], confStart),
      fetch(urls[1], confStart),
      fetch(urls[2], confEnd),
      fetch(urls[3], confEnd),
    ];

    Promise.all(fetches).then(responses =>
      Promise.all(responses.map(res => res.json()))
    ).then(jsons => {
      console.log("Loaded live data");

      const startFormData = jsons[0];
      const startFormConfig = jsons[1];
      const endFormData = jsons[2];
      const endFormConfig = jsons[3];

      // Process (clean-up) the API responses (respinses and config)
      // for the start and end surveys
      var startResponses = processResponses(startFormData),
        startConfig = processConfig(startFormConfig);

      var endResponses = processResponses(endFormData),
        endConfig = processConfig(endFormConfig);

      // Combine responses and config into a single object for both suveys
      var startCombined = combineQuestionResponses(startResponses, startConfig);
      var endCombined = combineQuestionResponses(endResponses, endConfig);

      // Combine both surveys into a single object for plotting
      var combined = combineStartEnd(startCombined, endCombined);

      resolve(combined);

    });


  });

};


module.exports = {
  load: load
};

module.exports = {
  load: load
};
