// App history management

var state = require("./state.js");
var config = require("./config.js");

const watcher = function(){

  window.onpopstate = function(){
    var params = params.getSearchParams();

    // Optionally do something
    // var state = require("./state.js");
    // state.pushHistory = false;

  };

};


var getSearchParams = function(){
  // Parse the search string of the URL
  // searchString = "?key1=value1&key2=value2"
  // returns dictionary of key:value pairs

  var searchString = window.location.search;

  // Split the string into components (ignore leading '?')
  var searchComps = searchString.substr(1).split("&");

  if (searchComps == "") {
    // No search parameters
    return {};
  }

  // Parse each parameter
  var params = {};
  for (var i = 0; i < searchComps.length; i++){
    var comp = searchComps[i].split("=", 2);

    if (comp.length == 2){
      params[comp[0]] = decodeURIComponent(comp[1].replace(/\+/g, " "));
    }
  }

  return params;
};



const setSearchParams = function(newParams){
  // Set the Search params in the URL and push a history entry

  var newSearch = "?";
  var firstSearch = true;
  for(var key in newParams){
    if(newParams[key] != null){
      if(firstSearch){
        firstSearch = false;
      } else {
        newSearch += "&";
      }
      newSearch += key + "=" + newParams[key];
    }
  }

  // Form the history name
  var histName = config.historyName;

  if(newParams.screen){
    histName += " - " + newParams.screen;
  }

  document.title = histName;

  if(state.pushHistory){
    history.pushState(newParams, histName, newSearch);
  } else {
    // Re-arm pushing history
    state.pushHistory = true;
  }

};


module.exports = {
  getSearchParams: getSearchParams,
  setSearchParams: setSearchParams,
  watcher: watcher
};
