// Module to process the data
// Common to dev and test

const combineStartEnd = function(start, end){
  // Combines start and end survey response objects

  var combined = [];

  // Loop through questions
  for(var i=0; i<start.length; i++){
    // Form combined result object

    // Deal with no data from second survey

    var endVal = null;
    if(end != null){
      endVal = end.find(r => r.question == start[i].question).responses;
    }

    var result = {
      question: start[i].question,
      responsesStart: start[i].responses,
      responsesEnd: endVal
    };

    combined.push(result);

  }

  return combined;

};

const combineQuestionResponses = function(resp, conf){
  // Combined question and response objects (ID lookup) for a single survey
  // Takes an object of form responses and an object with form configuration with options

  // Deal with null data
  if(resp.length == 0){
    return null;
  }

  var combined = [];

  // Loop through questions
  for(var i=0; i<conf.questions.length; i++){
    var qn = conf.questions[i];

    // Form combined result object
    var result = {
      question: qn.title,
      responses: {}
    };

    // Find response for the question and get the choice data
    var rsp = resp.find(r => r.id == qn.id);
    var cData = rsp.choiceData;

    // Loop through choices
    for(var j=0; j<conf.choices.length; j++){
      var ch = conf.choices[j];
      var val = cData.find(c => c.id == ch.id);
      if(typeof val == "undefined"){
        val = 0;
      } else {
        val = val.count;
      }
      result.responses[ch.title] = val;
    }

    combined.push(result);

  }

  return combined;

};

const processConfig = function(conf){
  // Process a forms configuration API response into required questions and choices

  // Remove metadata
  conf = conf.questions;

  var questions = [];
  var choices = [];

  for(var i=0; i<conf.length; i++){
    var qn = conf[i];
    if(qn.type != "Question.MatrixChoiceGroup"){
      questions.push({
        id: conf[i].id,
        title: conf[i].title
      });
    }
    else {
      for(var j=0; j<qn.choices.length; j++){
        choices.push({
          id: qn.choices[j].id,
          title: qn.choices[j].displayText
        });
      }

    }
  }

  return {questions: questions, choices: choices};

};

const processResponses = function(resp){
  // Process a forms responses API result

  // Remove metadata
  resp = resp.value;

  var responses = [];

  // Loop through response items
  for(var i=0; i<resp.length; i++){
    if(resp[i].columnType == "MatrixChoice"){

      responses.push({
        id: resp[i].columnPropertyName,
        choiceData: resp[i].choiceData
      });

    }

  }

  return responses;

};

module.exports = {
  combineStartEnd: combineStartEnd,
  combineQuestionResponses: combineQuestionResponses,
  processConfig: processConfig,
  processResponses: processResponses
};
