// Stores current app state

// App name in browser history
var historyName = "App";

// Data store
var data = null;

// History push control on nav
var pushHistory = true;

// Developer mode flag (for loading test data)
var devMode = null;

// Reference to the chart for redrawing
var chart = null;

// Survey mode (start or end)
var mode = "end";

// Refresh timer
var timer = null;
var refreshCount = 0;

module.exports = {
  historyName: historyName,
  data: data,
  pushHistory: pushHistory,
  devMode: devMode,
  chart: chart,
  timer: timer,
  refreshCount: refreshCount,
  mode: mode
};
