// SharePoint management module
const Digest = require("./digest.js").Digest;

// SharePoint digest
var spDigest = new Digest();

const APIurl = "_api/Web/";


const validateFetch = function(response) {
  // Checks a fetch response for HTTP code in 2xx or 3xx range

  if (response.status >= 200 && response.status < 400) {
    return response;
  } else {
    throw response.json();
  }
};


const readFiles = function(sitePath, folderPath){
  /* Gets all files in a given library and folder

    Parameters:
        sitePath (:obj:`str`): URL fragment pointing to the SharePoint
            (sub)-site where the list is held. The host can be excluded from
            the URL, e.g. sitePath="/sites/strat/mon/"
        folderPath (:obj:`str`): path to the folder to read, which
            must not start with a slash (e.g. use 'oswdocs/latest/')
  */


  return new Promise((resolve, reject) => {

    var url = sitePath + APIurl;
    url += "getfolderbyserverrelativeurl('"+folderPath+"')/files";


    fetch(url, {
      "method": "GET",
      "headers": {
        "Accept": "application/json;odata=verbose",
        "Content-Type": "application/json;odata=verbose"
      }
    })
      .then(validateFetch)
      .then(response => response.json())
      .then(json => {
        return resolve(json.d.results);
      })
      .catch(err => {
        return reject(err);
      });


  });

};


const writeFile = function(sitePath, folderPath, fName, content){
  /* Uploads a file to a SharePoint Document library

    Overwrites the file if it already exists.

    Parameters:
        sitePath (:obj:`str`): URL fragment pointing to the SharePoint
            (sub)-site where the list is held. The host can be excluded from
            the URL, e.g. sitePath="/sites/strat/mon/"
        folderPath (:obj:`str`): path to the folder in the site in which to
            save the file in, which must not start with a slash
            (e.g. use 'oswdocs/latest/')
        fName: (:obj:`str`): name of the file
        content (:obj:`str`): string to be uploaded to a file
  */


  return new Promise((resolve, reject) => {

    var url = sitePath + APIurl;
    url += "getfolderbyserverrelativeurl('"+folderPath+"')/files";
    url += "/add(overwrite=true,url='"+fName+"')";

    spDigest.refresh().then(() => {

      fetch(url, {
        method: "POST",
        headers: {
          "Accept": "application/json;odata=verbose",
          "X-RequestDigest": spDigest.digestString,
          "Content-Type": "application/json;odata=verbose"
        },
        body: content
      })
        .then(validateFetch)
        .then(response => response.json())
        .then(json => {
          return resolve(json.d);
        })
        .catch(err => {
          return reject(err);
        });

    });



  });


};

const deleteFile = function(sitePath, folderPath, fName){
  /* Uploads a file to a SharePoint Document library

  Parameters:
      sitePath (:obj:`str`): URL fragment pointing to the SharePoint
          (sub)-site where the list is held. The host can be excluded from
          the URL, e.g. sitePath="/sites/strat/mon/"
      folderPath (:obj:`str`): path to the folder in the site in which to
          save the file in, which must not start with a slash
          (e.g. use 'oswdocs/latest/')
      fName: (:obj:`str`): name of the file
  */

  return new Promise((resolve, reject) => {

    var url = sitePath + APIurl;
    url += "getfilebyserverrelativeurl('";
    url += sitePath + folderPath + fName + "')";

    spDigest.refresh().then(() => {

      fetch(url, {
        method: "POST",
        headers: {
          "Accept": "application/json;odata=verbose",
          "X-RequestDigest": spDigest.digestString,
          "X-HTTP-Method": "DELETE",
          "Content-Type": "application/json;odata=verbose"
        }
      })
        .then(validateFetch)
        .then(resp => {
          return resolve(resp);
        })
        .catch(err => {
          return reject(err);
        });

    });


  });


};


const readFile = function(sitePath, folderPath, fName){
  /* Downloads a file from the sharepoint are returns content

  Parameters:
      sitePath (:obj:`str`): URL fragment pointing to the SharePoint
          (sub)-site where the list is held. The host can be excluded from
          the URL, e.g. sitePath="/sites/strat/mon/"
      folderPath (:obj:`str`): path to the folder in the site in which to
          save the file in, which must not start with a slash
          (e.g. use 'oswdocs/latest/')
      fName: (:obj:`str`): name of the file
  */


  return new Promise((resolve, reject) => {

    var url = sitePath + folderPath + fName;

    fetch(url, {
      method: "POST",
      headers: {
        "Accept": "application/json;odata=verbose",
        "Content-Type": "application/json;odata=verbose"
      }
    })
      .then(validateFetch)
      .then(response => {
        return resolve(response) ;
      })
      .catch(err => {
        return reject(err);
      });

  });

};


const readItems = function(sitePath, listName){
  /*Gets the contents (rows) of a given list

  Parameters:
      sitePath (:obj:`str`): URL fragment pointing to the SharePoint
          (sub)-site where the list is held. The host can be excluded from
          the URL, e.g. sitePath="/sites/strat/mon/"
      listName (:obj:`str`): Name of the list to delete.
  */

  return new Promise((resolve, reject) => {

    var url = sitePath + APIurl;
    url += "lists/getByTitle('" + listName + "')/Items";

    fetch(url, {
      method: "GET",
      headers: {
        "Accept": "application/json;odata=verbose",
        "Content-Type": "application/json;odata=verbose"
      }
    })
      .then(validateFetch)
      .then(response => response.json())
      .then(json => {
        return resolve(json.d.results) ;
      })
      .catch(err => {
        return reject(err);
      });

  });

};


const getListItemType = function(sitePath, listName){
  /* Get a list item entity type for a given list.

    This function is used internally to find a value which is required for
    posting new items to a list.

    Parameters:
        sitePath (:obj:`str`): URL fragment pointing to the SharePoint
            (sub)-site where the list is held. The host can be excluded from
            the URL, e.g. sitePath="/sites/strat/mon/"
        listName (:obj:`str`): Name of the list to query.
  */

  return new Promise((resolve, reject) => {
    var url = sitePath + APIurl;
    url += "lists/getByTitle('" + listName + "')";

    fetch(url, {
      method: "GET",
      headers: {
        "Accept": "application/json;odata=verbose",
        "Content-Type": "application/json;odata=verbose"
      }
    })
      .then(validateFetch)
      .then(response => response.json())
      .then(json => {
        return resolve(json.d.ListItemEntityTypeFullName) ;
      })
      .catch(err => {
        return reject(err);
      });
  });

};

const addItem = function(sitePath, listName, itemDef){
  /* Adds a single item to a SharePoint list

    Parameters:
        sitePath (:obj:`str`): URL fragment pointing to the SharePoint
            (sub)-site where the list is held. The host can be excluded from
            the URL, e.g. sitePath="/sites/strat/mon/"
        listName (:obj:`str`): Name of the list to add an item to.
        itemDef (:obj:`dict`): Dictionary of item definition arguments, where
            keys should represent the fields in the list.
  */

  return new Promise((resolve, reject) => {

    // Check/refresh digest
    spDigest.refresh().then(() => {
      // Get list item type
      getListItemType(sitePath, listName).then(itemType => {

        itemDef["__metadata"] = {"type": itemType};

        // Form the URL and make the request
        var url = sitePath + APIurl;
        url += "lists/getByTitle('" + listName + "')/Items";

        fetch(url, {
          method: "POST",
          headers: {
            "Accept": "application/json;odata=verbose",
            "X-RequestDigest": spDigest.digestString,
            "Content-Type": "application/json;odata=verbose"
          },
          body: JSON.stringify(itemDef)
        })
          .then(validateFetch)
          .then(response => response.json())
          .then(json => {
            return resolve(json.d) ;
          })
          .catch(err => {
            return reject(err);
          });

      });
    });
  });

};

module.exports = {
  spDigest: spDigest,
  readFiles: readFiles,
  writeFile: writeFile,
  deleteFile: deleteFile,
  readFile: readFile,
  getListItemType: getListItemType,
  readItems: readItems,
  addItem: addItem
};
