// Theme management

const config = require("./config.js");

var setButton = function(){
  document.getElementById("themeButton").addEventListener("click", themeToggle);
};

var checkPreference = function(){
  // Only using light theme
  if(!config.darkModeAvailable){
    return;
  }

  var dk = "(prefers-color-scheme: dark)";
  var lt = "(prefers-color-scheme: light)";
  if (window.matchMedia && window.matchMedia(dk).matches){
    document.getElementsByTagName("html")[0].classList.add("darkTheme");
  }
  if (window.matchMedia && window.matchMedia(lt).matches){
    document.getElementsByTagName("html")[0].classList.remove("darkTheme");
  }
};

var themeToggle = function(){
  document.getElementsByTagName("html")[0].classList.toggle("darkTheme");
};

var watcher = function(){
  // Only using light theme
  if(!config.darkModeAvailable){
    return;
  }


  window.matchMedia("(prefers-color-scheme: dark)")
    .addEventListener("change", function(event){
      if (event.matches) {
        document.getElementsByTagName("html")[0].classList.add("darkTheme");
      } else {
        document.getElementsByTagName("html")[0].classList.remove("darkTheme");
      }
    });
};

// Expose the page rendering function
module.exports = {
  checkPreference: checkPreference,
  themeToggle: themeToggle,
  setButton: setButton,
  watcher: watcher
};
