// SharePoint digest management

class Digest{

  digestString;
  digestTime;
  expiry;
  sitePath;

  constructor(){
    this.digestString = null;
    this.digestTime = new Date();
    this.expiry = 1000 * 60 * 5;
    this.sitePath = null;
  }

  get valid(){
    if(this.digestString == null){
      return false;
    }

    var now = new Date();
    var age = now - this.digestTime;
    if(age >= this.expiry){
      return false;
    }

    return true;
  }

  refresh(){
    let d = this;

    return new Promise((resolve, reject) =>{
      if(d.valid){
        return resolve();
      }

      var url = d.sitePath + "_api/contextinfo";

      fetch(url, {
        "method": "POST",
        "headers": {
          "Accept": "application/json;odata=verbose",
          "Content-Type": "application/json;odata=verbose"
        }
      })
        .then(validateFetch)
        .then(response => response.json())
        .then(json => {
          d.digestString = json.d.GetContextWebInformation.FormDigestValue;
          d.digestTime = new Date();
          return resolve();
        })
        .catch(err => {
          return reject(err);
        });

    });

  }

}


const validateFetch = function(response) {
  // Checks a fetch response for HTTP code in 2xx or 3xx range

  if (response.status >= 200 && response.status < 400) {
    return response;
  } else {
    throw Error(response.statusText);
  }
};


module.exports = {
  Digest: Digest
};
