// Shows the chart screen

const Barchart = require("../components/_barchart.js").Barchart;
const screenTemplate = require("../../html/views/view_chart.html");
var state = require("../state.js");
var config = require("../config.js");

const render = function(){

  // Add template HTML structure
  var contentHolder = document.getElementById("contentHolder");
  contentHolder.innerHTML = "";
  contentHolder.innerHTML = screenTemplate;

  // Update the QR codes
  var qr = document.getElementById("qrImg");
  qr.setAttribute("src", require("../../img/"+state.mode+".png"));

  var qrM = document.getElementById("qrImgModal");
  qrM.setAttribute("src", require("../../img/"+state.mode+".png"));

  if(state.data.getN(state.mode) < config.minResponses){
    var modal = document.getElementById("modal-QR");
    modal.style.visibility = "visible";
  }

  // Update the background image
  var bg = document.getElementById("bgImg");
  bg.setAttribute("src", require("../../img/cricket.jpeg"));

  // Render the chart
  var data = state.data.getChartData();
  state.chart = new Barchart("#chartHolder", data, config.questionOrder);
  state.chart.draw();
};

module.exports = {
  render: render
};
