// Dev (test) data loader for development

const startFormData = require("./startForm-data.json");
const startFormConfig = require("./startForm-config.json");
const endFormData = require("./endForm-data.json");
const endFormConfig = require("./endForm-config.json");


const combineStartEnd = require("./process.js").combineStartEnd;
const combineQuestionResponses = require("./process.js").combineQuestionResponses;
const processConfig = require("./process.js").processConfig;
const processResponses = require("./process.js").processResponses;


const load = function(){
  return new Promise((resolve) => {

    // Process (clean-up) the API responses (respinses and config)
    // for the start and end surveys
    var startResponses = processResponses(startFormData),
      startConfig = processConfig(startFormConfig);

    var endResponses = processResponses(endFormData),
      endConfig = processConfig(endFormConfig);

    // Combine responses and config into a single object for both suveys
    var startCombined = combineQuestionResponses(startResponses, startConfig);
    var endCombined = combineQuestionResponses(endResponses, endConfig);

    // Combine both surveys into a single object for plotting
    var combined = combineStartEnd(startCombined, endCombined);

    resolve(combined);

  });

};


module.exports = {
  load: load
};
