// Main app script

// Import style sheet
import "../sass/app.sass";

var state = require("./state.js");

const theme = require("./theme.js"),
  sidebar = require("./sidebar.js"),
  config = require("./config.js"),
  version = require("./version.js"),
  history = require("./history.js"),
  sp = require("./sharepoint/sharepoint.js"),
  DataStore = require("./model/DataStore.js").DataStore,
  viewController = require("./viewController.js");

// Initial app script
var init = function(){
  // Show load / initial screen
  viewController.showBlank();

  // Set up nav bar (left)
  sidebar.setup();

  // Check dark theme user preference
  theme.checkPreference();
  theme.setButton();
  theme.watcher();

  // Check search parameters if required
  var params = history.getSearchParams();
  if("mode" in params){
    if(params.mode == "start" || params.mode == "end"){
      state.mode = params.mode;
    }
  }


  // Register a history state handler (back/fwd buttons)
  history.watcher();

  // Set tab name
  document.title = config.appName;

  // Initialise a data store
  state.data = new DataStore();

  // Show draft tag
  if(config.draft){
    document.getElementById("draftTag").style.display = "block";
  }

  // Configure SharePoint module
  sp.spDigest.sitePath = config.sitePath;

  // Start app here with a data load
  state.devMode = false;
  for(var i of ["localhost", "10.0.", ".dev"]){
    if(window.location.hostname.indexOf(i) != -1){
      state.devMode = true;
    }
  }

  //state.devMode = false

  // Start app here with a data load
  refreshTimer();

};

// Data loader and screen refresh
const refresh = function(){

  var dataPromise;
  if(state.devMode){
    console.warn("Local development - using test data");
    dataPromise = require("./data/dev.js").load;
  } else {
    console.log("Loading live data");
    dataPromise = require("./data/live.js").load;
  }

  // Load all data then start app logic
  Promise.all([dataPromise()])
    .then(d => {
      // Do something with the loaded data (load model objects?)
      state.data.loadData(d[0]);

      // Hand-off to a viewController to show content on screen
      sidebar.populate();

      viewController.showChart();

    })
    .catch(err => console.warn(err));

};

// Refresh timer function
const refreshTimer = function(){
  if(state.refreshCount > 150){
    console.log("No more refreshes allowed");
  } else {
    refresh();
    state.refreshCount++;
    state.timer = setTimeout(refreshTimer, 1500);
  }
};


// Check the DOM is ready and Start the app
if(document.readyState != "loading"){
  init();
} else {
  // Wait to start the app
  document.addEventListener("DOMContentLoaded", init);
}


//Register a window resize handler
window.addEventListener("resize", windowResizeHandler);

function windowResizeHandler(){
  window.requestAnimationFrame(function(){
    // Do something here
    if(state.chart != null){
      state.chart.draw();
    }

  });
}


// Top level export for debugging
export {state, config, version, sp, refresh};
