/* Data store object responsible for loading and saving content

*/
// var config = require("../config.js");
// const chart = require("../views/chart");

class DataStore{

  formsData;

  constructor(){
    this.formsData = {};
  }



  loadData(formsData){
    /* Loads data from the Forms API

    Parameters:
      formsData {data object defined by Forms}
    */

    this.formsData = formsData;

  }

  getN(rType){
    // Gives the n value for a given response type "start" or "end"
    var responses = null;
    if(rType == "start"){
      responses = this.formsData[0]["responsesStart"];
    }
    if(rType == "end"){
      responses = this.formsData[0]["responsesEnd"];
    }

    if(responses == null){
      return 0;
    }

    return Object.values(responses).reduce((a, b) => a + b, 0);

  }

  getChartData(){
    // Pre-process form data for use by the bar chart
    // Returns percentages for (strongly) agree, neutral and (strongly) disagree per series

    var rTypes = ["responsesStart", "responsesEnd"];

    // Check if in start mode
    if(require("../state.js").mode == "start"){
      rTypes = ["responsesStart"];
    }

    // Check for single form condition (no second form data)
    if(this.formsData[0].responsesEnd == null){
      rTypes = ["responsesStart"];
    }


    var chartData = [];

    for(var q of this.formsData){
      var series = [];
      for(var r of rTypes){

        var subSeries = [];

        var resps = q[r];

        // Get total
        var n = resps["Strongly Agree"];
        n += resps["Agree"];
        n += resps["Neutral"];
        n += resps["Strongly Disagree"];
        n += resps["Disagree"];

        // Get percentages for the three categories
        subSeries.push((resps["Strongly Agree"] + resps["Agree"]) / n);
        subSeries.push(resps["Neutral"] / n);
        subSeries.push((resps["Strongly Disagree"] + resps["Disagree"]) / n);

        series.push(subSeries);

      }
      chartData.push(series);

    }

    return chartData;

  }


}

module.exports = {
  DataStore: DataStore
};
