const d3 = require("d3");

class Barchart{

  constructor(selector, data, labels){
    this.options = {
      selector: selector,
      margin: {
        left: 300,
        right: 100,
        top: 45,
        bottom: 45
      },
      paddingInner: 0.4,
      paddingOuter: 0.25,
      barHeight: 50,
      labelWidth: 290
    };
    this.data = data;
    this.labels = labels;
  }



  draw(redraw){
    var opt = this.options;
    var svgHolder = document.querySelector(opt.selector);

    // Clear contents inside the holding element (remove loading messages)
    if(!redraw){
      svgHolder.innerHTML = "";
    }

    // Size the holder and append the svg element
    var svgWidth = parseFloat(getComputedStyle(svgHolder).width),
      svgHeight = parseFloat(getComputedStyle(svgHolder).height);

    // Add SVG content element
    var holder = d3.select(opt.selector);
    var svg = holder.selectAll("svg").data([0]);

    var svgNew = svg.enter().append("svg");
    svg = svgNew.merge(svg);
    svg.attr("class", "barchart")
      .attr("width", svgWidth)
      .attr("height", svgHeight);

    var svgContent = svg.selectAll("g .svgContent")
      .data([0]).enter().append("g")
      .attr("class", "svgContent")
      .attr("transform", "translate("+opt.margin.left+"," + opt.margin.top + ")");

    // Add bounding rectangle guide
    var boundingRect = svgContent.selectAll("rect").data([0]);
    boundingRect.exit().remove();

    var boundingRectNew = boundingRect.enter().append("rect");
    boundingRect = boundingRectNew.merge(boundingRect);
    boundingRect.attr("id", "boundingRect")
      .attr("x", 0)
      .attr("y", 0)
      .attr("width", svgWidth - opt.margin.left - opt.margin.right)
      .attr("height", svgHeight - opt.margin.top - opt.margin.bottom);

    // Add series holder (bottom)
    var seriesHolder = svgContent.selectAll("g #seriesHolder")
      .data([0]).enter()
      .append("g")
      .attr("id", "seriesHolder");

    // Add delta labels holder
    var deltaHolder = svgContent.selectAll("g #deltaHolder")
      .data([0]).enter()
      .append("g")
      .attr("id", "deltaHolder");

    // Add legend holder
    var legGrp = svgContent.selectAll("g #legend")
      .data([0]).enter()
      .append("g")
      .attr("id", "legend");

    // Make scales
    var xScale = d3.scaleLinear()
      .range([0, svgWidth - opt.margin.left - opt.margin.right])
      .domain([0, 1]);

    var yScale = d3.scaleBand()
      .range([0, svgHeight - opt.margin.top - opt.margin.bottom])
      .domain(this.labels)
      .paddingInner(opt.paddingInner)
      .paddingOuter(opt.paddingOuter);


    var axGrp = svgContent.append("g")
      .attr("id", "axisGroup");

    var xAxis = d3.axisBottom()
      .scale(xScale)
      .tickSizeOuter(0)
      .ticks(4, ".0%");

    var xAxisGrp = axGrp.append("g")
      .attr("class", "xAxis axis")
      .attr("transform", "translate(0," + (svgHeight - opt.margin.top - opt.margin.bottom) + ")");


    xAxisGrp.call(xAxis);

    var yAxis = d3.axisLeft()
      .scale(yScale)
      .tickSizeOuter(0);

    var yAxisGrp = axGrp.append("g")
      .attr("class", "yAxis axis");

    yAxisGrp.call(yAxis);

    yAxisGrp.selectAll(".tick text")
      .call(this.textWrap, opt.labelWidth)
      .attr("transform", function(){
        var h = this.getBoundingClientRect().height;
        return "translate(0,-" + (h/2) + ")";
      });

    // Add a group per question series
    var seriesGrp = seriesHolder.selectAll("g").data(this.data);
    seriesGrp.exit().remove();

    var seriesGrpNew = seriesGrp.enter().append("g");
    seriesGrp = seriesGrpNew.merge(seriesGrp);
    seriesGrp.attr("class", "series")
      .attr("id", (d,i) => "series-" + i)
      .attr("transform",(d,i) => "translate(0," + yScale(this.labels[i]) + ")");

    // Add rectangles per (sub)series
    for(var ser in this.data){

      // Add a group per survey sub-series
      seriesGrp = d3.select("#series-" + ser);
      var subserGrp = seriesGrp.selectAll("g").data(this.data[ser]);
      subserGrp.exit().remove();

      var subserGrpNew = subserGrp.enter().append("g");
      subserGrp = subserGrpNew.merge(subserGrp);
      subserGrp.attr("class", "series")
        .attr("id", (d,i) => "subSeries-" + i)
        .attr("transform",(d,i) => {
          var offset = i * (yScale.bandwidth()/this.data[ser].length);
          return "translate(0," + offset + ")";
        });

      for(var subser in this.data[ser]){

        var seriesData = [];
        var counter = 0;
        for(var d of this.data[ser][subser]){
          seriesData.push({
            start: counter,
            width: d
          });
          counter += d;
        }

        seriesGrp = d3.select("#series-" + ser);
        subserGrp = seriesGrp.select("#subSeries-" + subser);

        var seriesRects = subserGrp.selectAll("rect").data(seriesData);
        seriesRects.exit().remove();

        var seriesRectsNew = seriesRects.enter().append("rect");
        seriesRects = seriesRectsNew.merge(seriesRects);
        seriesRects.attr("class", "seriesBlock")
          .attr("id", (d,i) => "seriesBlock-" + i)
          .attr("x", (d) => xScale(d.start))
          .attr("y", 0)
          .attr("width", (d) => xScale(d.width))
          .attr("height", yScale.bandwidth()/this.data[ser].length);


        var blockLabels = subserGrp.selectAll("text").data(seriesData);
        blockLabels.exit().remove();

        var blockLabelsNew = blockLabels.enter().append("text");
        blockLabels = blockLabelsNew.merge(blockLabels);
        blockLabels.attr("class", (d) => {
          if(d.width < 0.03){
            return "blockLabel blockLabelHidden";
          }
          return "blockLabel";
        })
          .attr("id", (d,i) => "blockLabel-" + i)
          .attr("x", (d) => xScale(d.start) + xScale(d.width / 2))
          .attr("y", (yScale.bandwidth()/(this.data[ser].length*2)))
          .text(d => Math.round(d.width*100) + "%");


      }

    }

    // Add the legend

    var xCounter = 0,
      yCounter = 0;

    var legWidth = svgWidth - opt.margin.left - opt.margin.right;

    // Loop through series items and add legend entries
    var legendEntries = [
      {"label": "(Strongly) Agree", "classes": ["legendGreen"]},
      {"label": "Neutral", "classes": ["legendOrange"]},
      {"label": "(Strongly) Disagree", "classes": ["legendRed"]}
    ];

    // Define legend defaults
    const legDefaults = {
      "pathWidth": 30,
      "pathTextGap": 5,
      "textGapRight": 15,
      "rowGap": 10
    };

    for(var s in legendEntries){
      if(legendEntries[s].label != null){
        var seriesClasses = legendEntries[s].classes.join(" ");

        var legEntry = legGrp.append("g")
          .attr("class", "legEntry " + seriesClasses);

        var txtWidth, textHeight;

        var legText = legEntry.append("text")
          .attr("dominant-baseline", "middle")
          .text(legendEntries[s].label)
          .attr("text-anchor", "left")
          .attr("font-size", "2vmin")
          .each(function(){
            txtWidth = this.getBBox().width;
            textHeight = this.getBBox().height;
          });

        // Calculate the position of the legend entry
        var legEntryWidth = legDefaults.pathWidth + legDefaults.pathTextGap;
        legEntryWidth += txtWidth + legDefaults.textGapRight;

        // Check if overflows the row
        if(xCounter + legEntryWidth > legWidth){
          xCounter = 0;
          yCounter = yCounter + textHeight + legDefaults.rowGap;
        }

        var midPoint = yCounter + (textHeight / 2);

        legText.attr("x", xCounter + legDefaults.pathWidth + legDefaults.pathTextGap)
          .attr("y", midPoint + 1);

        // Calculate legend line path
        var legPath;
        legPath = "M" + xCounter + " " + yCounter + " L";
        legPath += (xCounter + legDefaults.pathWidth) + " " + yCounter;
        legPath += " L" + (xCounter + legDefaults.pathWidth) + " " + (yCounter + textHeight);
        legPath += " L" + xCounter + " " + (yCounter + textHeight);

        legEntry.append("path")
          .attr("d", legPath)
          .attr("stroke", "black");

        // Increment the place for the next item
        xCounter = xCounter + legEntryWidth;
      }
    }


    // Right-align the legend vs the graph
    // Can prevent close with axis label

    var legWidthCalc = legGrp.node().getBBox().width;
    var rightAlign = (svgWidth - opt.margin.left - opt.margin.right - legWidthCalc);
    legGrp.attr("transform", "translate(" + rightAlign + "," + 0 + ")");





    // If only a single bar, stop here
    if(this.data[0].length == 1){
      return;
    }

    // Function for calculating deltas
    const getDelta = function(series){
      var startVal = series[0][0] + series[0][1];
      var endVal = series[1][0] + series[1][1];
      return endVal - startVal;
    };

    // Add arrowheads
    var arrowHeads = deltaHolder.selectAll("path.arrowHead").data(this.data);
    arrowHeads.exit().remove();

    var arrowHeadsNew = arrowHeads.enter().append("path");
    arrowHeads = arrowHeadsNew.merge(arrowHeads);
    arrowHeads.attr("class", d => {
      var classStr = "arrowHead";
      if(getDelta(d) >= 0){
        classStr += " arrowHeadPositive";
      } else {
        classStr += " arrowHeadNegative";
      }
      return classStr;
    })
      .attr("id", (d,i) => "arrowHead-" + i)
      .attr("d", (d,i) => {
        var startX = xScale(1);

        var midPointY = yScale(this.labels[i]);
        var endY = midPointY + (yScale.bandwidth()*7/8);

        var arrW = 15;
        var arrH = 10;

        var path = d3.path();
        path.moveTo(startX - 3, endY);
        path.lineTo(startX + arrW, endY - arrH);
        path.lineTo(startX + arrW, endY + arrH);
        path.lineTo(startX - 3, endY);

        return path.toString();
      });



    // Add delta paths
    var deltaArrows = deltaHolder.selectAll("path.deltaPath").data(this.data);
    deltaArrows.exit().remove();

    var deltaArrowsNew = deltaArrows.enter().append("path");
    deltaArrows = deltaArrowsNew.merge(deltaArrows);
    deltaArrows.attr("class", d => {
      var classStr = "deltaPath";
      if(getDelta(d) >= 0){
        classStr += " deltaPathPositive";
      } else {
        classStr += " deltaPathNegative";
      }
      return classStr;
    })
      .attr("id", (d,i) => "deltaPath-" + i)
      .attr("d", (d,i) => {
        var startX = xScale(1);
        var endX = xScale(1) + 50;

        var midPointY = yScale(this.labels[i]);
        var startY = midPointY + (yScale.bandwidth()*1/8);
        var endY = midPointY + (yScale.bandwidth()*7/8);

        var path = d3.path();
        path.moveTo(startX, startY);
        path.lineTo(endX, startY);
        path.lineTo(endX, endY);
        path.lineTo(startX, endY);

        return path.toString();
      });

    // Add delta circles
    var deltaCircles = deltaHolder.selectAll("circle").data(this.data);
    deltaCircles.exit().remove();

    var deltaCirclesNew = deltaCircles.enter().append("circle");
    deltaCircles = deltaCirclesNew.merge(deltaCircles);
    deltaCircles.attr("class", d => {
      var classStr = "deltaCircle";
      if(getDelta(d) >= 0){
        classStr += " deltaCirclePositive";
      } else {
        classStr += " deltaCircleNegative";
      }
      return classStr;
    })
      .attr("id", (d,i) => "deltaCircle-" + i)
      .attr("cx", xScale(1) + 50)
      .attr("cy", (d,i) => (yScale.bandwidth()/2) + yScale(this.labels[i]))
      .attr("r", (yScale.bandwidth()/4));

    // Add delta values
    var deltaLabels = deltaHolder.selectAll("text").data(this.data);
    deltaLabels.exit().remove();

    var deltaLabelsNew = deltaLabels.enter().append("text");
    deltaLabels = deltaLabelsNew.merge(deltaLabels);
    deltaLabels.attr("class", "deltaLabel")
      .attr("id", (d,i) => "deltaLabel-" + i)
      // .attr("x", (d) => xScale(d.start) + xScale(d.width / 2))
      .attr("x", xScale(1) + 50)
      .attr("y", (d,i) => (yScale.bandwidth()/2) + yScale(this.labels[i]))
      .text(d => Math.round(getDelta(d)*100) + "%");


  }

  redraw(){
    this.draw(true);
  }

  textWrap(text, width){
    text.each(function() {
      var text = d3.select(this),
        words = text.text().split(/\s+/).reverse(),
        word,
        line = [],
        lineNumber = 0,
        lineHeight = 1.1, // em
        y = text.attr("y"),
        x = text.attr("x"),
        dy = parseFloat(text.attr("dy")),
        tspan = text.text(null).append("tspan").attr("x", x).attr("y", y).attr("dy", dy + "em");

      // eslint-disable-next-line no-cond-assign
      while (word = words.pop()) {
        line.push(word);
        tspan.text(line.join(" "));
        if (tspan.node().getComputedTextLength() > width) {
          line.pop();
          tspan.text(line.join(" "));
          line = [word];
          tspan = text.append("tspan").attr("x", x).attr("y", ++lineNumber * lineHeight + dy + "em").text(word);
        }
      }
    });
  }

}


module.exports = {
  Barchart: Barchart
};
