// Navigation bar (left) controllers

const config = require("./config.js");
const version = require("./version.js").version;
var state = require("./state.js");

const populate = function(){
  // Populate sidebar

  var sbContent = document.getElementById("sidebarContent");
  sbContent.innerText = "";

};




const setup = function(){
  var menuButton = document.querySelector("#menuButton");
  menuButton.onclick = toggleSideBar;

  // Set version number
  document.getElementById("appVersionNumber").innerText = "v" + version.v;
  for(var elem of document.getElementsByClassName("appName")){
    elem.innerText = config.appName;
  }

  // No sidebar for this app (display app)
  hideSideBar();

};

const toggleSideBar = function(){
  var nav = document.querySelector("#sideBar");
  if(getComputedStyle(nav,null).display === "none"){
    nav.style.display = "block";
  } else {
    nav.style.display = "none";
  }
};

const hideSideBar = function(){
  var nav = document.querySelector("#sideBar");
  if(getComputedStyle(nav,null).display != "none"){
    nav.style.display = "none";
  }
};

module.exports = {
  populate: populate,
  hideSideBar: hideSideBar,
  toggleSideBar: toggleSideBar,
  setup: setup
};
